* {
  box-sizing: border-box;
  background-color: rgb(169, 92, 104);
  color:rgb(225, 182, 52);
  }

  .font-link {
    font-family:'EB Garamond', serif;
  }
  
  body {
  margin: 0;
  }

    /* links at header and footer */
  .header, .footer {
  padding: 50px 0px 0px 0px;
  font-size: 24px;
  display: inline;
  }
  
  /* hi there i'm candice header title */
  .header h1 {
  font-size: 68px;
  display: inline-flex;
  padding: 40px;
  margin: 40px;
  }

  /* about and work titles */
  .heading h1 {
    font-size: 48px;
  }
  
  /* links at nav and footer  */
  .navigation, .bottom {
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 24px;
  }

  /* learn more footer title */
  .footer h1 {
  display: inline;
  padding: 40px;
  margin: 40px;
  }
  
  /* navigation and footer link layout */
  .navigation ul, .bottom ul {
  list-style: none;
  display: inline;
  align-items: flex-end;
  }
  
  .navigation ul li, .bottom ul li {
  display: inline;
  margin: 30px;
  padding: 20px;
  }
  
  /* about, contact and portfolio sections  */
  .about, .portfolio {
  padding: 40px;
  margin: 40px;
  }
  
  .about p {
  display: flex;
  flex-wrap: wrap;
  text-align: flex-start;
  font-size: 20px;
  line-height: 1.5;
  padding: 40px 0px;
  }
  
  .project {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  font-size: 16px;
  transition: width 0.5s linear;
  justify-content: space-between;
  padding: 0px 15px 15px 15px;
  }
  
  .project-img, .about-img {
  width: 45%;
  margin: 5px;
  /* width: 500px; */
  }
            
  .project-img img, .about-img img {
  width: 100%;
  }
  
  /* individual projects titles */
  .project h2 {    
  padding: 20px 10px 5px 10px;
  font-size: 22px;
  text-align: center;
  }
  
  /* github and deployed app links */
  .project-links {
  display: flex;
  flex-wrap: inline-block;
  justify-content: space-around;
  font-size: 20px;
  align-items: center;
  }
  
  .github-link:hover {
  opacity: 0.7;
  }
    
  .deployed-link:hover {
  opacity: 0.7;
  }
  
